
#SpaceX{
    .launchContainer {
        margin-top: 20px;
    }

    .launch__mission__name {
        font-weight: 700;
    }

    .launch__copy {
        margin-bottom: 3px;
    }

    .launch__details {
        height: 250px;
        overflow-y: auto;
        padding-right: 10px;
    }

    .launch__details::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        background-color: transparent;
    }

    .launch__details::-webkit-scrollbar {
        width: 9px;
        background-color: transparent;
    }

    .launch__details::-webkit-scrollbar-thumb {
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        background-color: rgba(0, 0, 0, .5)
    }


    .launch__img {
        width: auto;
        display: block;
        height: 250px;
        float: right;
        margin-left: 20px;
    }

    .launch__tooltip {
        position: relative;
        display: block;
        border-bottom: 1px dotted black;
        cursor: pointer;
        width: fit-content;
    }

    .launch__tooltip .launch__tooltiptext {
    visibility: hidden;
    background-color: #000;
    color: #fff;
    text-align: left;
    border-radius: 6px;
    padding: 20px;
    width: 350px;

    /* Position the tooltip */
    position: absolute;
    z-index: 1;
    }

    .launch__tooltip:hover .launch__tooltiptext {
        visibility: visible;
    }
}